export default {
  name: 'customService',
  data () {
    return {
      info: null
    }
  },
  methods: {
    getData () {
      this.$http(this.$urlPath.customService, {
        identity: this.$ids.identity,
        store_id: this.$ids.storeId
      }, '', (data) => {
        if (data.data) {
          this.info = data.data
        }
      }, (errorCode, error) => {
        this.$toast(error)
      })
    },
	callPhone () {
		uni.makePhoneCall({
			phoneNumber: this.info.phone
		})
	}
  },
  onLoad () {
    this.getData()
  }
}
